import { mapState, mapActions } from 'vuex'
import { upyunUrl } from '../../../api/config'

export default {
  data () {
    return {
      upyunUrl,
      oJson: '',
      companyVal: '',
      officeVal: '',
      postVal: '',
      initialStatus: false,
      dateStart: '',
      dateEnd: ''
    }
  },
  onLoad (options) {
    const data = JSON.parse(options.data)
    this.oJson = data
    this.initialStatus = false
    this.companyVal = data.company
    this.officeVal = data.office
    this.postVal = data.word_des
    this.dateStart = data.start_time
    this.dateEnd = data.end_time
  },
  onShow () {
    if (!this.oJson) this.onEmptyFn()
  },
  computed: {
    ...mapState([
      'userInfo'
    ])
  },
  methods: {
    ...mapActions([
      'isWorkInfo'
    ]),
    // 选择开始日期
    bindDateChange (e) {
      this.dateStart = e.target.value
    },
    // 选择结束日期
    bindDateEndChange (e) {
      this.dateEnd = e.target.value
    },
    // 清空数据
    onEmptyFn () {
      this.companyVal = ''
      this.officeVal = ''
      this.postVal = ''
      this.dateStart = ''
      this.dateEnd = ''
    },
    // 保存工作经历
    onSaveFn () {
      if (this.initialStatus) return false
      const companyVal = this.companyVal
      const officeVal = this.officeVal
      const postVal = this.postVal
      const dateStart = this.dateStart
      const dateEnd = this.dateEnd
      const data = {
        that: this,
        companyVal,
        officeVal,
        postVal,
        dateStart,
        dateEnd
      }
      if (!companyVal) {
        this.showToastFn('公司名称不能为空!')
        return false
      }
      if (!officeVal) {
        this.showToastFn('职位不能为空!')
        return false
      }
      if (!postVal) {
        this.showToastFn('职位状态不能为空!')
        return false
      }
      if (!dateStart) {
        this.showToastFn('请选择开始时间!')
        return false
      }
      if (!dateEnd) {
        this.showToastFn('请选择结束时间!')
        return false
      }
      uni.showLoading({ title: 'Loading' })
      this.initialStatus = true
      this.isWorkInfo(data).then(() => {
        if (this.oJson) {
          this.showToastFn('修改成功')
        } else {
          this.showToastFn('添加成功')
        }
        setTimeout(() => {
          uni.navigateBack()
        }, 1500)
        uni.hideLoading()
      })
    },
    showToastFn (title) {
      uni.showToast({
        title,
        icon: 'none',
        duration: 1500
      })
    }
  },
  onUnload () {
    this.onEmptyFn()
  }
}
